.NavBar-link {
    font-weight: bold;
    color: black;
    text-decoration: none;
    margin: 0 1.4em;
    text-align: center;
    white-space: nowrap;
}
.NavBar-link:hover {
    color: var(--lab-red);
}
.NavBar-link-activated {
    color: var(--lab-red);
    text-decoration: underline;
}

.NavBar-trailing {
    margin-left: 2em;
    display: inline-flex;
    overflow-y: auto;
    align-items: center;
}

.NavBar-container {
    display: flex;
    justify-content: space-between;
    margin: 1.5em 2em;
}

.NavBar-logo {
    width: 5em;
}

.NavBar-icon {
    width: 1.6em;
}