@font-face {
  font-family: 'CoconPro';
  src: url("./fonts/CoconPro.otf") format('otf');
}

.App {
  --background-color: #EFEFEF;
  --lab-red: #E00000;
  font-family: 'Open Sans', 'Roboto';
  background-color: var(--background-color);
  background: linear-gradient(180deg, rgba(239,239,239,1) 0%, rgba(211,211,211,1) 100%);
  display: inline-block;
  min-height: 100vh;
  max-width: 100%;
  width: 100%;
}

.App-body {
  margin: 0 2em;
}


h1 {
  font-family: 'Cocon', cursive;
  font-size: 2em;
  margin: 0.1em 0;
}

p {
  margin: 0.5em 0;
  max-width: 30em;
}

label {
  font-size: 0.8em;
  color: gray;
}