.RoundedTextField-input {
    border: none!important;
    font-family: 'Open Sans', 'Roboto';
    font-weight: bold;
    padding: 0.5em 1em;
    outline: none;
    border-radius: 5em;
    color: rgb(48, 48, 48);
    font-size: 0.9em;
    display: inline-block;
}

.LabComponents-split-section-container {
    display: flex;
    justify-content: center;
    align-items: center;
}
  
@media (max-width: 60em) {
    .LabComponents-split-section-container {
        display: flex;
        flex-direction: column;
    }
    .LabComponents-split-section {
        margin: 2em 0!important;
        align-items: center!important;
        text-align: center!important;

        width: 100% !important;
        max-width: 100% !important;
    }
}
  
.LabComponents-split-section {
    margin: 2em 2em;
    display: flex;
    flex-direction: column;

    text-align: center;
    align-items: center;
    /* flex-basis: 50%; */
    width: 50%;
    max-width: 50%;
}
.LabComponents-main-section {
    margin: 2em 0;
    align-items: center;
    display: flex;
    flex-direction: column;
}